@import url('https://fonts.googleapis.com/css?family=Poppins:400,600&display=swap&subset=latin-ext');
// global
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html {
   scroll-behavior:smooth
}
body {
   font-family: 'Poppins', sans-serif;
   color: #091E2B;
   font-size: 14px;
}
p {
   line-height: 1.786;
}
h1,h2,h3,h4,h5,h6 {
   font-weight: 400;
}
a {
   text-decoration: none;
   transition: all 0.4s;
   color: initial;
}
a:hover {
   opacity: 0.6;
}
a>img:hover {
   opacity: 0.6;
}
img {
   max-width: 100%;
   height: auto;
}

.btn {
   background: #6C7DE3;
   display: inline-block;
   text-transform: uppercase;
   font-size: 18px;
   color: #fff;
   padding: 15px 120px;
   cursor: pointer;
}
// global

//hero
.hero {
   &__wrapper {
      display: grid;
      grid-template-columns: 375px auto 50%;
      grid-template-rows: 70px auto auto;
   }
   &__margin {
      background-color: #FFC625;
   }
   &__right {
      grid-row: -1/-4;
      grid-column: 3;
      img {
         display: block;
         width: 100%;
      }
   }
   &__left {
      position: relative;
      grid-column: 2/3;
   }
   &__logo {
      line-height: 70px;
      grid-column: 2;
      img {
         vertical-align: middle;
      }
   }
   &__margin {
      background-color: #FFC625;
      grid-column: 1;
      grid-row: 2;
   }
   &__body {
      background-color: #FFC625;
      padding: 100px 40px 80px 0;
      position: relative;
      height: 100%;
      h1 {
         font-size: 60px;
         line-height: 1.383;
         position: relative;
         color: #000;
      }
      h1::after {
         content: url(../../img/ico/lines.svg);
         position: absolute;
         right: 0;
         bottom: 0;
         transform: translate(110%, -70%);
      }
   }
   &__text {
      margin: 45px 40px 90px 0;
   }
   &__dots {
      position: absolute;
      right: 0;
      display: block;
      margin-top: 40px;
      transform: translateX(50%);
   }
   &__btn {
      position: relative;
   }
   &__btn::after {
      content: url(../../img/ico/arrow.svg);
      position: absolute;
      left: 50px;
      bottom: 0;
      transform: rotate(90deg) translateX(10px);
   }
   &__title {
      grid-column: 2;
      margin-top: 45px;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      h2 {
      font-weight: 600;
      font-size: 60px;
      color: #231F20;
      }
   }
}
// end hero

// section 
.content {
   margin: 80px 0;
   display: grid;
   grid-template-columns: 375px 1fr 1fr 375px;
   h3 {
      font-size: 36px;
      color: #231F20;
      padding-left: 65px;
      position: relative;
   }
   &__icon {
      display: block;
      margin-left: auto;
      margin-right: 45px;
   }
   &__left {
      grid-column: 2;
      p {
         margin: 15px 20px 0 110px;
      }
      h3::before {
         content: url(../../img/img/head_2.svg);
         z-index: -1;
         left: 0;
         position: absolute;
         bottom: 7px;
      }
   }
   &__right {
      grid-column: 3;
      p {
         margin: 15px 10px 0 110px;
      }
      h3::before {
         content: url(../../img/img/head_1.svg);
         z-index: -1;
         left: 0;
         position: absolute;
         bottom: 7px;
      }
   }
}
// end section 

// footer 
.footer {
   margin: 40px 0;
   display: grid;
   grid-template-columns: 375px 1fr 375px;
   p {
      color: #7E7E7E;
   }
   &__content {
      grid-column: 2;
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
}
// end footer 

// responsvie
@media screen and (max-width: 1790px) {
   .hero__wrapper {
      grid-template-columns: minmax(0, 375px) auto 50%;
   }
   .hero__title {
      grid-column: 1 / span 3;
      align-items: center;
   }
   .content {
      grid-template-columns: 120px auto auto 120px;
   }
   .footer {
      grid-template-columns: 120px auto 120px;
   }
}

@media screen and (max-width: 1490px) {
   .hero__logo {
      grid-column: 1/3;
      text-align: center;
   }
   .hero__body h1, .hero__title h2 {
      font-size: 42px;
   }
   .hero__margin {
      display: none;
   }
   .hero__left {
      grid-column: 1/3;
   }
   .hero__body {
      padding: 80px 20px;
   }
   .hero__body h1::after {
      transform: translateX(110%);
      right: 20px;
   }
   .content h3 {
      font-size: 26px
   }
   .footer {
      grid-template-columns: 120px auto 120px;
   }
}

@media screen and (max-width: 1268px) {
   .hero__logo, .hero__left {
      grid-column: 1/4;
   }
   .hero__body h1 {
      text-align: center;
   }
   .hero__right {
      grid-column: 1/4;
      grid-row: -1/-3;
   }
   .hero__body h1::after {
      display: none;
   }
   .hero__text {
      position: relative;
   }
   .hero__dots {
      transform: none;
   }
   .hero__btn {
      display: block;
      max-width: 346px;
      margin: auto;
   }
   .content__left, .content__right {
      margin: 20px 0;
      grid-column: 2/4;
   }
}
@media screen and (max-width: 991px) {
   .content {
      grid-template-columns: 50px auto auto 50px;
   }
   .footer {
      grid-template-columns: 50px auto 50px;
   }
}
@media screen and (max-width: 991px) {
   body {
      font-size: 12px;
   }
   .hero__body h1, .hero__title h2 {
      font-size: 30px;
   }
   .btn {
      font-size: 14px;
   }
   .content__left p, .content__right p {
      margin-left: 65px;
   }
   .content h3 {
      font-size: 22px;
   }
   .hero__body {
      padding: 40px 15px;
   }
   .content {
      grid-template-columns: 15px auto auto 15px;
   }
   .footer {
      grid-template-columns: 15px auto 15px;
   }
   .footer__content {
      font-size: 10px;
   }
}
// end responsive